.q-wrap {
	display: none;
	position: relative;
	width: 100%;
	height: 100%;
	min-height: 480px;
}

.q-wrap.active {
	display: block;
}

/*--- Role info ---*/
.role {
	position: relative;
	width: 16.2em;
	margin: 0 auto;
	padding-top: 2em;
	text-align: center;
	line-height: 1.5;
}

.role-name {
	margin: 0;
	font-size: 1.5em;
	line-height: 1;
}

.role-tagline {
	margin-top: 0.375em;
	margin-bottom: 1em;
	color: @theme;
}

.role-profile {
	margin: 0;
}
/*--- Role info END ---*/

.opt-list {
	position: relative;
/*	left: 50%;
	bottom: 2em;
	transform: translate3d(-50%,0,0);*/
	width: (186 + 120px);
	height: (108 + 140px);
	margin: 3em auto 0;
}

.opt {
	position: absolute;
}

.opt-a {
	top: 0;
	left: 0;
}

.opt-b {
	top: 0;
	left: 124px;
}

.opt-c {
	top: 108px;
	left: 62px;
}

.opt-d {
	top: 108px;
	left: 186px;
}

.avatar {
	width: 120px;
	height: 140px;
	background-size: cover;
	clip-path: polygon(0 25%, 50% 0, 100% 25%, 100% 75%, 50% 100%, 0 75%);
}

.actor-name {
	position: absolute;
	font-size: 0.8125em;
	margin: 0;	
}

.opt-a .actor-name,
.opt-b .actor-name {
	top: -0.3em;
	right: 0;
}

.opt-c .actor-name,
.opt-d .actor-name {
	bottom: -0.3em;
	left: 0;
}

/*--- Hexagon border ---*/
.hex-border {
	position: absolute;
	top: 0;
	left: 0;
	width: 120px;
	/*height: 69.28px;*/
	height: 70px;
	margin: 34.64px 0;
	border-left: solid 2px white;
	border-right: solid 2px white;
	box-sizing: border-box;
	opacity: 0.25;
}

.hex-border::before,
.hex-border::after {
	content: "";
	position: absolute;
	z-index: 100;
	left: 15.5736px;
	width: 84.85px;
	height: 84.85px;
	box-sizing: border-box;
	transform: scaleY(0.5774) rotate(-45deg);
}

.hex-border::before {
	top: -42.4264px;
	border-top: solid 2.8284px white;
	border-right: solid 2.8284px white;
}

.hex-border::after {
	bottom: -42.4264px;
	border-bottom: solid 2.8284px #ffffff;
	border-left: solid 2.8284px #ffffff;
}

.opt.active .hex-border,
.opt.active .hex-border::before,
.opt.active .hex-border::after {
	border-color: @theme;
	opacity: 1;
}

.opt.active .actor-name {
	color: @theme;	
}
/*--- Hexagon border END ---*/
